body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
h1,
h2 {
  text-align: center;
  text-shadow: -1px -1px #fff, -2px -2px #fff, -1px 1px #fff, -2px 2px #fff,
    1px 1px #fff, 2px 2px #fff, 1px -1px #fff, 2px -2px #fff, -3px -3px 2px #bbb,
    -3px 3px 2px #bbb, 3px 3px 2px #bbb, 3px -3px 2px #bbb;
  color: steelblue;
}
form {
  width: 500px;
  margin: 0 auto;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
}
label {
  text-align: center;
  margin-bottom: 5px;
}
input {
  width: 200px;
}
span {
  display: flex;
  width: 250px;
  flex-direction: column;
  align-items: center;
  margin-bottom: 20px;
}
.item {
  display: flex;
  flex-direction: column;
  align-items: center;
}
ul {
  margin: auto;
  width: 500px;
  height: 100px;
  overflow-y: scroll;
  background-color: #ffffff;
}
li {
  display: flex;
}
.cont {
  width: 500px;
  margin: auto;
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-bottom: 30px;
}
button {
  margin-left: 10px;
}
